.contacts {
  color: $def-white-color;
  display: flex;

  @media (max-width: $laptop-width) {
    justify-content: space-between;
  }

  @media (max-width: 600px) {
    max-width: 235px;
  }
}

.contacts__item:not(:last-child) {
  margin-right: 90px;

  @media (max-width: 1400px) {
    margin-right: 47px;
  }
}

.contacts__item:nth-last-child(2) {
  @media (max-width: 1490px) {
    margin-right: 0;
  }
}

.contacts__item {
  max-width: 265px;
  font-size: 18px;
  line-height: 1.16;
  font-weight: 400;

  @media (max-width: 1750px) {
    font-size: 16px;
    line-height: 1.18;
    max-width: 249px;
  }

  @media (max-width: 600px) {
    display: none;
  }
}

.contacts__item:nth-child(4) {
  @media (max-width: 1163px) {
    display: none;
  }
}

.contacts__item:nth-last-child(2) {
  @media (max-width: 978px) {
    display: none;
  }
}

.logo-tablet {
  display: none;

  @media (max-width: 978px) {
    display: block;
    transform: translateY(-20px);
  }
}

.contacts-text {
  color: $def-white-color;
}

.contacts-days {
  color: $days-color;
  margin-left: 10px;
  font-size: 16px;
  line-height: 1.18;

  @media (max-width: $laptop-width) {
    font-size: 14px;
    line-height: 1.14;
  }

  @media (max-width: 750px) {
    display: none;
  }

  &--mob {
    display: none;

    @media (max-width: 750px) {
      display: block;
      margin-left: 0;
    }
  }
}

.contacts__telemetry {
  position: relative;
  margin-left: auto;
  display: block;

  @media (max-width: $laptop-width) {
    font-size: 16px;
    line-height: 1.18;
  }

  &::before {
    position: absolute;
    content: "";
    width: 18px;
    height: 24px;
    left: -30px;
    top: 0;
    background-image: url("../img/icon/telemetry.svg");
    background-repeat: no-repeat;

    @media (max-width: 1750px) {
      left: 108px;
    }
  }

  &:hover {
    opacity: 0.7;
  }
}

.contacts__telemetry:hover::before {
  transform: rotate(360deg);
  transition: $telemetry-transition-settings;
}

.contacts__telemetry--aside {
  display: none;

  @media (max-width: 1750px) {
    display: block;
  }
}

.contacts__telemetry--laptop {
  @media (max-width: 1750px) {
    display: none;
    transform: rotate(360deg);
  }
}

.aside-address--logo {
  @media (max-width: 600px) {
    display: none;
  }
}

.contacts__item--mob {
  display: none;

  @media (max-width: 600px) {
    display: block;
  }
}

.number-button {
  display: none;
  background-color: transparent;
  width: 23px;
  height: 23px;
  position: absolute;
  right: 55px;
  top: 0;

  &::before {
    position: absolute;
    content: "";
    width: 23px;
    height: 23px;
    top: 0;
    background-image: url("../img/icon/number-button.svg");
    background-repeat: no-repeat;
  }

  @media (max-width: 600px) {
    display: block;
  }
}