.gasification {
  position: relative;
  color: $def-black-color;
  margin-bottom: 140px;

  @media (max-width: $laptop-width) {
    margin-bottom: 80px;
  }

  @media (max-width: $tablet-width) {
    margin-bottom: 60px;
  }

  @media (max-width: 600px) {
    margin-bottom: 35px;
  }
}

.gasification__block {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.gasification__text-title {
  font-size: 65px;
  line-height: 1.16;
  max-width: 700px;
  font-weight: 500;
  margin-bottom: 30px;

  @media (max-width: $laptop-width) {
    font-size: 50px;
    line-height: 1.18;
    max-width: 535px;
  }

  @media (max-width: $tablet-width) {
    font-size: 40px;
  }

  @media (max-width: 600px) {
    font-size: 30px;
    line-height: 1.16;
  }
}

.gasification__text-description {
  max-width: 690px;
  font-weight: 400;
  margin-bottom: 40px;

  @media (max-width: $laptop-width) {
    font-size: 18px;
    line-height: 1.35;
    max-width: 497px;
  }

  @media (max-width: $tablet-width) {
    font-size: 16px;
    line-height: 1.35;
    max-width: 442px;
  }
}

.gasification-images {
  display: flex;
}

.gasification-img {
  @media (max-width: 1640px) {
    width: 372px;
    height: 382px;
  }

  @media (max-width: $tablet-width) {
    width: 244px;
    height: 251px;
  }

  @media (max-width: 703px) {
    display: none;
  }
}

.gasification-item {
  min-width: 156px;
}

.gasification-item:not(:last-child) {
  margin-right: 20px;

  @media (max-width: 600px) {
    margin-right: 17px;
  }
}

.gasification-item:nth-child(2) {
  @media (max-width: 600px) {
    margin-right: 0;
  }
}

.gasification-item:last-child {
  @media (max-width: 600px) {
    display: none;
  }
}

.gaz-img {
  @media (max-width: $laptop-width) {
    width: 172px;
    height: 126px;
  }
}

.gasification-link {
  font-weight: 400;
  border-bottom: 1px solid $light-gray-color;
  margin-top: 40px;
  display: block;
  max-width: 150px;

  @media (max-width: $laptop-width) {
    font-size: 18px;
    line-height: 1.35;
    max-width: 108px;
    margin-top: 27px;
  }

  @media (max-width: $tablet-width) {
    font-size: 16px;
  }

  @media (max-width: 600px) {
    margin-top: 17px;
  }
}