.advantages-form {
  margin-top: 143px;
  color: $def-black-color;
  height: 358px;
  background-repeat: no-repeat;
  background-image: url("../img/car.png");

  @media (max-width: 1650px) {
    margin-top: 130px;
    background-image: url("../img/car-laptop.png");
  }

  @media (max-width: 1200px) {
    background-image: url("../img/white-block.svg");
    max-width: 640px;
    margin-top: 60px;
    height: 330px;
  }

  @media (max-width: 600px) {
    background-color: $def-white-color;
  }
}

.advantages-form-wrapper {
  padding-left: 138px;
  padding-top: 45px;
  z-index: 1;

  @media (max-width: $laptop-width) {
    padding-left: 75px;
  }

  @media (max-width: 1650px) {
    padding-left: 75px;
  }

  @media (max-width: $tablet-width) {
    padding-left: 30px;
  }

  @media (max-width: 600px) {
    padding-left: 15px;
  }
}

.advantages-form-car {
  position: absolute;
  z-index: 2;
}

.advantages-form-title {
  display: block;
  font-size: 55px;
  line-height: 1.16;
  margin-bottom: -15px;

  @media (max-width: 1650px) {
    font-size: 50px;
    line-height: 1.18;
  }

  @media (max-width: $tablet-width) {
    font-size: 40px;
  }

  @media (max-width: 600px) {
    font-size: 30px;
    line-height: 1.16;
  }
}

.advantages-form-text {
  font-size: 20px;
  line-height: 1.5;

  @media (max-width: 1650px) {
    font-size: 18px;
  }

  @media (max-width: 600px) {
    line-height: 1.16;
  }
}

.form {
  display: flex;
  margin-bottom: 25px;

  @media (max-width: 600px) {
    font-size: 16px;
    margin-bottom: 20px;
  }
}

.form-tel {
  background-color: $form-color;
  border: none;
  width: 334px;
  height: 56px;
  padding-left: 15px;

  @media (max-width: 1650px) {
    width: 240px;
  }

  @media (max-width: 600px) {
    width: 205px;
    font-size: 14px;
  }
}

.form-button {
  width: 233px;
  height: 56px;
  background-color: $form-button-color;
  cursor: pointer;

  @media (max-width: 1650px) {
    width: 191px;
  }

  @media (max-width: 600px) {
    width: 124px;
  }

  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.5;
  }
}

.advantages-form-description {
  font-size: 13px;
  line-height: 1.3;
  color: $form-description;
  max-width: 462px;

  @media (max-width: 600px) {
    font-size: 11px;
  }
}