.advantages {
  background-color: $advantages-color;
  padding-bottom: 145px;
  padding-top: 35px;

  @media (max-width: $laptop-width) {
    padding-bottom: 120px;
  }

  @media (max-width: $tablet-width) {
    padding-bottom: 55px;
  }

  @media (max-width: 600px) {
      padding-bottom: 0;
    }

  .wrapper {
    padding-right: 60px;

    @media (max-width: $tablet-width) {
      padding-right: 30px;
      padding-left: 30px;
    }

    @media (max-width: 600px) {
      padding-right: 15px;
      padding-left: 15px;
    }
  }
}

.advantages-list {
  margin-top: 85px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @media (max-width: $tablet-width) {
    margin-top: 70px;
  }
}

.advantages-title {
  font-size: 65px;
  line-height: 1.16;
  font-weight: 500;

  @media (max-width: $laptop-width) {
    font-size: 50px;
    line-height: 1.18;
  }

  @media (max-width: $tablet-width) {
    font-size: 40px;
    line-height: 1.18;
  }
}

.advantages-item {
  position: relative;
  max-width: 455px;
  height: 293px;
  margin-bottom: 90px;

  @media (max-width: 1710px) {
    max-width: 368px;
  }

  @media (max-width: $laptop-width) {
    margin-bottom: 25px;
    max-width: 310px;
  }

  @media (max-width: $mobile-width) {
    height: auto;
    min-width: 335px;
    border-top: 1px solid $gray-border;
    padding-top: 15px;
  }

  &::before {
    position: absolute;
    content: "";
    width: 71px;
    height: 60px;
    top: 0;
    left: 0;
    background-repeat: no-repeat;
    background-image: url("../img/icon/advantages-1.svg");
  }
}

.advantages-item__title {
  position: relative;
  font-size: 25px;
  line-height: 1.16;
  font-weight: 500;
  max-width: 317px;
  text-decoration: underline;
  text-decoration-color: $blue-border;
  padding-top: 80px;

  @media (max-width: $laptop-width) {
    font-size: 22px;
    line-height: 1.18;
  }

  @media (max-width: $tablet-width) {
    font-size: 20px;
    line-height: 1.15;
  }

  @media (max-width: $mobile-width) {
    padding-top: 0;
  }

  &::after {
    @media (max-width: $mobile-width) {
      position: absolute;
      content: "";
      margin-left: 10px;
      top: 5px;
      width: 19px;
      height: 15px;
      background-repeat: no-repeat;
      background-image: url("../img/icon/advantages-arrow.svg");
    }
  }
}

.advantages-item:hover .advantages-item__title::after {
  transform: rotate(-90deg);
}

.title-2 {
  max-width: 259px;
}

.title-3 {
  max-width: 259px;
}

.title-4 {
  max-width: 274px;
}

.title-5 {
  max-width: 202px;
}

.title-6 {
  max-width: 154px;
}

.title-7 {
  max-width: 187px;

  @media (max-width: $mobile-width) {
    max-width: 100%;
  }
}

.title-8 {
  max-width: 182px;

  @media (max-width: $mobile-width) {
    max-width: 100%;
  }
}

.title-9 {
  max-width: 280px;
}

.advantages-item:nth-child(2) {
  &::before {
    width: 64px;
    height: 62px;
    background-image: url("../img/icon/advantages-2.svg");
  }
}

.advantages-item:nth-child(3) {
  &::before {
    width: 81px;
    height: 54px;
    background-image: url("../img/icon/advantages-3.svg");
  }
}

.advantages-item:nth-child(4) {
  &::before {
    width: 58px;
    height: 58px;
    background-image: url("../img/icon/advantages-4.svg");
  }
}

.advantages-item:nth-child(5) {
  &::before {
    width: 61px;
    height: 64px;
    background-image: url("../img/icon/advantages-5.svg");
  }
}

.advantages-item:nth-child(6) {
  &::before {
    width: 86px;
    height: 61px;
    background-image: url("../img/icon/advantages-6.svg");
  }
}

.advantages-item:nth-child(7) {
  &::before {
    width: 68px;
    height: 69px;
    background-image: url("../img/icon/advantages-7.svg");
  }
}

.advantages-item:nth-child(8) {
  &::before {
    width: 67px;
    height: 66px;
    background-image: url("../img/icon/advantages-8.svg");
  }
}

.advantages-item:nth-child(9) {
  margin-bottom: 0;

  &::before {
    width: 114px;
    height: 58px;
    background-image: url("../img/icon/advantages-9.svg");
  }
}

.advantages-item::before {
  @media (max-width: $mobile-width) {
    display: none;
  }
}

.advantages-item__text {
  line-height: 1.35;
  font-weight: 400;
  margin-top: auto;

  @media (max-width: $laptop-width) {
    font-size: 18px;
  }

  @media (max-width: $tablet-width) {
    font-size: 16px;
  }

  @media (max-width: $mobile-width) {
    display: none;
  }
}

.advantages-item:hover .advantages-item__text {
  @media (max-width: $mobile-width) {
    display: block;
  }
}