.details {
  display: flex;
  justify-content: space-between;

  @media (max-width: 600px) {
    overflow-x: auto;
  }
}

.details-item:first-child {
  min-width: 331px;

  @media (max-width: 1550px) {
    min-width: 238px;
  }
}

.details-item:nth-child(2) {
  min-width: 382px;

  @media (max-width: 1550px) {
    min-width: 275px;
  }
}

.details-item:last-child {
  min-width: 319px;

  @media (max-width: 1550px) {
    min-width: 255px;
  }
}

.details-number {
  font-size: 85px;
  line-height: 1.32;
  margin: 0;

  @media (max-width: $laptop-width) {
    font-size: 60px;
    line-height: 1.35;
  }

  @media (max-width: $tablet-width) {
    font-size: 50px;
  }
}

.details-title {
  line-height: 1.16;
  max-width: 335px;
  display: block;

  @media (max-width: $laptop-width) {
    font-size: 20px;
    line-height: 1.15;
    max-width: 267px;
  }

  @media (max-width: $tablet-width) {
    font-size: 16px;
    line-height: 1.18;
  }
}

.details-description {
  font-weight: 400;
  max-width: 381px;

  @media (max-width: $laptop-width) {
    font-size: 18px;
    line-height: 1.35;
    max-width: 274px;
  }

  @media (max-width: $tablet-width) {
    font-size: 16px;
    line-height: 1.18;
  }
}