.aside {
  grid-area: aside;
  width: 238px;
  background-color: $def-black-color;

  @media (max-width: 978px) {
    display: none;
  }
}

.aside-wrapper {
  padding: 25px 30px 25px 30px;
}

.aside-address {
  color: $def-white-color;
  font-style: normal;
  font-size: 17px;
  font-weight: 400;
  line-height: 1.34;
  margin-top: 5px;
  margin-left: 15px;
  width: 135px;
  border-bottom: 1px dashed $gray-border;

  @media (max-width: 978px) {
    font-size: 14px;
    line-height: 1.15;
    width: 110px;
    margin-left: 0;
  }
}