.agzs {
  display: flex;
  flex-direction: column;
  position: relative;
  color: $def-black-color;

  @media (max-width: $laptop-width) {
  }
}

.agzs__block {
  display: flex;
  justify-content: space-between;
  flex-direction: row-reverse;
  align-items: center;

  @media (max-width: 600px) {
    flex-direction: column;
    align-items: flex-start;
  }
}

.agzs__block-title {
  font-size: 65px;
  line-height: 1.16;
  max-width: 323px;
  font-weight: 500;
  margin-bottom: 30px;

  @media (max-width: $laptop-width) {
    font-size: 50px;
    line-height: 1.18;
    max-width: 249px;
  }

  @media (max-width: $tablet-width) {
    font-size: 40px;
  }

  @media (max-width: 600px) {
    font-size: 30px;
    line-height: 1.16;
  }
}

.agzs__block-description {
  max-width: 571px;
  font-weight: 400;

  @media (max-width: $laptop-width) {
    font-size: 18px;
    line-height: 1.35;
    max-width: 411px;
  }

  @media (max-width: $tablet-width) {
    font-size: 16px;
    max-width: 366px;
  }
}

.agzs-img {
  @media (max-width: 1640px) {
    width: 408px;
    height: 247px;
  }

  @media (max-width: $tablet-width) {
    width: 311px;
    height: 188px;
  }
}