.page {
  height: 100%;
  box-sizing: border-box;
}

.page-body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 500;
  font-size: 25px;
  line-height: 1.33;
  background-color: white;
  counter-reset: section;
  overflow-x: hidden;
  color: $def-white-color;
  display: flex;
  background-color: $content-color;

  @media (max-width: $tablet-width) {
    display: block;
  }
}
