.gaz-delivery {
  background-repeat: no-repeat;
  background-image: url("../img/index-bg.jpg");
  background-position: top;
  background-size: cover;
  height: 882px;
  padding-top: 55px;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/index-bg@2x.jpg");
  }

  @media (max-width: $laptop-width) {
    height: 750px;
    background-image: url("../img/index-bg-laptop.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/index-bg-laptop@2x.jpg");
    }
  }

  @media (max-width: $tablet-width) {
    background-image: url("../img/index-bg-tablet.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/index-bg-tablet@2x.jpg");
    }
  }

  @media (max-width: 600px) {
    padding-top: 30px;
    height: 595px;
    background-image: url("../img/index-bg-mob.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/index-bg-mob@2x.jpg");
    }
  }

  .wrapper {
    padding-right: 15px;

    @media (max-width: 1208px) {
      padding-left: 30px;
    }

    @media (max-width: 600px) {
      padding-left: 15px;
    }
  }
}

.gaz-delivery__title {
  font-size: 70px;
  line-height: 1.17;
  color: $def-white-color;
  max-width: 782px;

  @media (max-width: $laptop-width) {
    font-size: 60px;
    line-height: 1.16;
  }

  @media (max-width: $tablet-width) {
    font-size: 50px;
    line-height: 1.18;
  }

  @media (max-width: 600px) {
    font-size: 40px;
  }

  .line {
    color: $def-white-color;
  }
}