.nav {
  margin-top: 67px;
}

.nav__item:hover {
  opacity: 0.7;
}

.nav__item:not(:last-child) {
  margin-bottom: 25px;
}

.nav-link {
  color: $nav-text-color;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.12;
}

.burger-nav--closed {
  right: -100%;
  top: -122px;
}

.burger-nav {
  position: absolute;
  transition: $default-transition-settings;

  @media (min-width: 979px) {
    display: none;
  }

  .nav__list {
    position: absolute;
    background-color: $def-black-color;
    height: 750px;
    width: 238px;
    right: -100%;
    padding-top: 35px;
    padding-left: 35px;

    @media (max-width: 600px) {
      height: 595px;
      top: 25px;
    }
  }
}

.burger-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  border: none;
  outline: none;
  width: 28px;
  height: 21px;
  background-color: transparent;
  z-index: 2;
  transition: $default-transition-settings;

  @media (min-width: 979px) {
    display: none;
  }

  &::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: $burger-color;
    box-shadow: 0 9px 0 0 $burger-color, 0 18px 0 0   $burger-color;
    transition: $default-transition-settings;
  }
}

.burger-nav__toggle--opened {
  position: absolute;
  top: -15px;
  right: 0;
  cursor: pointer;
  border: none;
  outline: none;
  width: 28px;
  height: 21px;
  background-color: transparent;
  z-index: 2;
  transition: $default-transition-settings;

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: 9px;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: $burger-color;
    transform: rotate(-45deg);
    transition: $default-transition-settings;
  }

  &::after {
    transform: rotate(45deg);
  }
}

.burger-nav--opened {
  right: -20px;
  top: -122px;
}