.gaz-delivery__price {
  font-weight: 500;
  background-color: $def-black-color;
  border-radius: 10px;
  max-width: 558px;
  margin-top: 360px;

  @media (max-width: $laptop-width) {
    margin-top: 260px;
    max-width: 523px;
  }

  @media (max-width: 600px) {
    margin-top: 135px;
  }
}

.price {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding-left: 40px;

  @media (max-width: 600px) {
    padding-bottom: 35px;
  }
}

.price-number {
  font-size: 65px;
  line-height: 0;
  color: $def-blue-color;
  margin-right: 22px;

  @media (max-width: $laptop-width) {
    font-size: 60px;
  }

  @media (max-width: $tablet-width) {
    font-size: 50px;
    line-height: 1.18;
  }

  @media (max-width: 600px) {
    width: 100%;
    margin-top: 25px;
    margin-bottom: 0;
  }
}

.price-text {
  max-width: 320px;
  font-size: 20px;
  line-height: 1.15;
  margin-bottom: 5px;
  margin-top: 5px;

  @media (max-width: $laptop-width) {
    font-size: 18px;
    line-height: 1.16;
  }

  @media (max-width: 600px) {
    font-size: 16px;
    line-height: 1.18;
    margin-bottom: -2px;
  }

  &--date {
    color: $def-yellow-color;
  }
}

.price-link {
  color: $def-white-color;
  font-size: 18px;
  line-height: 1.16;
  padding-bottom: 2px;
  border-bottom: 1px solid $blue-border;

  @media (max-width: 600px) {
    font-size: 16px;
    line-height: 1.18;
  }
}