.calculators {
  margin-top: 134px;
  margin-bottom: 100px;

  @media (max-width: $laptop-width) {
    margin-top: 100px;
  }

  @media (max-width: $tablet-width) {
    margin-top: 85px;
  }

  @media (max-width: 600px) {
    margin-top: 75px;
    margin-bottom: 65px;
  }
}

.calculators-title {
  position: relative;
  font-size: 65px;
  line-height: 1.16;
  max-width: 448px;
  font-weight: 500;
  color: $def-black-color;
  margin-bottom: 35px;
  padding-left: 45px;

  @media (max-width: $laptop-width) {
    font-size: 50px;
    line-height: 1.18;
    padding-left: 55px;
  }

  @media (max-width: $tablet-width) {
    font-size: 40px;
    margin-bottom: 30px;
  }

  @media (max-width: 600px) {
    font-size: 30px;
    line-height: 1.16;
    padding-left: 30px;
  }

  &::before {
    position: absolute;
    content: "";
    width: 43px;
    height: 50px;
    left: 0;
    bottom: 13px;
    background-repeat: no-repeat;
    background-image: url("../img/icon/calculator.svg");

    @media (max-width: $laptop-width) {
      width: 33px;
      height: 39px;
      background-image: url("../img/icon/calculator-laptop.svg");
    }

    @media (max-width: 600px) {
      width: 24px;
      height: 28px;
      bottom: 5px;
      background-image: url("../img/icon/calculator-mob.svg");
    }
  }
}

.calculators-list {
  display: flex;

  @media (max-width: 1650px) {
    justify-content: space-between;
    max-width: 900px;
  }

  @media (max-width: 1279px) {
    flex-wrap: wrap;
    max-width: 705px;
    margin: 0 auto;
  }

  @media (max-width: 750px) {
    flex-direction: column;
  }
}

.calculators-item {
  display: block;
  width: 387px;
  height: 201px;
  background-color: $calculator1-color;
  padding-left: 35px;
  padding-top: 25px;
  background-repeat: no-repeat;
  background-image: url("../img/calc-img-1.png");
  background-size: cover;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/calc-img-1@2x.png");
  }

  @media (max-width: 1650px) {
    width: 277px;
    height: 190px;
    background-image: url("../img/calc-img-1-laptop.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/calc-img-1-laptop@2x.png");
    }
  }

  @media (max-width: 1279px) {
    width: 341px;
    background-image: url("../img/calc-img-1-tablet.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/calc-img-1-tablet@2x.png");
    }
  }

  @media (max-width: 750px) {
    width: 329px;
    background-image: url("../img/calc-img-1-mob.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/calc-img-1-mob@2x.png");
    }
  }
}

.calculators-item:not(:last-child) {
  margin-right: 60px;

  @media (max-width: 1650px) {
    margin-right: 30px;
  }

  @media (max-width: $laptop-width) {
    margin-right: 0;
  }

  @media (max-width: 750px) {
    margin-bottom: 15px;
  }
}

.calculators-item:last-child {
  @media (max-width: 1279px) {
    margin-top: 23px;
    margin-left: auto;
    margin-right: auto;
  }

  @media (max-width: 750px) {
    margin: 0;
  }
}

