.calculators-name {
  line-height: 1.16;
  color: $def-white-color;
  max-width: 155px;
  border-bottom: 1px solid $def-white-color;

  @media (max-width: 1650px) {
    font-size: 20px;
    line-height: 1.15;
  }
}

.calculators-description {
  font-size: 20px;
  line-height: 1.35;
  font-weight: 400;
  max-width: 253px;

  @media (max-width: 1650px) {
    font-size: 16px;
  }
}

.calculators-1 .calculators-description {
  @media (max-width: $tablet-width) {
