.product-description {
  margin-top: 55px;
  color: $def-black-color;
  margin-bottom: 110px;

  @media (max-width: $laptop-width) {
    margin-bottom: 90px;
    margin-top: 90px;
  }

  @media (max-width: $tablet-width) {
    margin-top: 40px;
  }

  @media (max-width: 600px) {
    margin-bottom: 35px;
  }

  .wrapper {
    @media (max-width: 555px) {
      padding-right: 0;
    }
  }
}

.product-description-wrapper {
  min-width: $mobile-width;
  width: 100%;
  padding: 0 75px 0 75px;
}

.product-description__about-us {
  max-width: 875px;
  margin-bottom: 55px;

  @media (max-width: $laptop-width) {
    font-size: 18px;
    line-height: 1.5;
    max-width: 630px;
  }

  @media (max-width: $tablet-width) {
    font-size: 16px;
    margin-bottom: 45px;
  }
}