.footer {
  grid-area: footer;
  background-color: $footer-background;
  font-size: 18px;
  line-height: 1.16;
  font-weight: 400;
  padding-bottom: 35px;
  padding-top: 50px;

  @media (max-width: $laptop-width) {
    font-size: 16px;
    line-height: 1.18;
  }

  @media (max-width: 600px) {
      padding-top: 35px;
      padding-bottom: 25px;
    }
}

.footer-wrapper {
  max-width: 1335px;
  width: 100%;
  padding: 0 75px;

  @media (max-width: $tablet-width) {
    padding: 0 30px;
  }

  @media (max-width: 600px) {
      padding: 0 15px;
    }
}

.footer-cities {
  display: flex;
  justify-content: space-between;

  @media (max-width: $tablet-width) {
    flex-direction: column;
  }
}

.city-line {
  display: none;

  @media (max-width: $tablet-width) {
    display: block;
    height: 1px;
    width: 705px;
    background-color: $city-line-color;
  }
}

.city-1 {
  display: flex;
  justify-content: space-between;
  max-width: 587px;
  flex-wrap: wrap;

  @media (max-width: $laptop-width) {
    max-width: 520px;
  }

  @media (max-width: $tablet-width) {
    max-width: 560px;
  }

  @media (max-width: 555px) {
    max-width: 290px;
  }
}

.city__item {
  min-width: 257px;
  margin-bottom: 15px;

  @media (max-width: 600px) {
    font-size: 14px;
    line-height: 1.14;
  }

  @media (max-width: 555px) {
    min-width: 120px;
  }
}

.city-1 .city__item:first-child {
  width: 100%;
  margin-bottom: 25px;
}

.city-2 {
  @media (max-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 560px;
    margin-top: 15px;
  }

  @media (max-width: 555px) {
    max-width: 290px;
  }
}

.city-2 .city__item:first-child {
  margin-bottom: 50px;

  @media (max-width: $tablet-width) {
    width: 100%;
    margin-bottom: 25px;
  }
}

.city-2 .city__item {
  margin-bottom: 45px;
}

.city-name {
  &--1 {
    color: $city-color1;
  }

  &--2  {
    color: $city-color2;
  }
}

.footer-copyright {
  margin-top: 60px;
  font-size: 13px;
  line-height: 1.3;

  @media (max-width: 600px) {
    margin-top: 10px;
  }
}

.footer-link {
  font-size: 18px;
  line-height: 1.16;
  color: $def-white-color;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);

  @media (max-width: 600px) {
    font-size: 14px;
    line-height: 1.14;
  }
}

.footer-link-2 {
  color: $days-color;
  margin-left: 30px;

  @media (max-width: 600px) {
    font-size: 14px;
    line-height: 1.14;
  }
}

.footer-copyright-text {
  color: $days-color;
  max-width: 767px;
  margin-top: 20px;

  @media (max-width: 600px) {
    font-size: 11px;
  }
}